.card {
  display: flex;
  width: 600px;
  margin-bottom: 2rem;
  padding: 1rem;
  box-shadow: 0 0 10px #ccc;
  border-radius: 1rem;
  /* border: 1px solid steelblue; */
}

.card__left {
  height: 100%;
  /* border: 1px solid tomato; */
}

.card__image {
  width: 100px;
  height: 100px;
}

.card__image > img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
}

.card__right {
  width: 450px;
  height: 100%;
  padding: 0 1rem;
  /* border: 1px solid darkred; */
}

.card__actions {
  display: flex;
  justify-content: flex-end;
  /* border: 1px solid darkviolet; */
}

.card__actions > i {
  font-size: 1.3rem;
  color: #1da1f2;
  cursor: pointer;
}
